import React from "react"
import {graphql} from "gatsby"
import Layout from "../Components/Layout";
import '../Styles/Global.css'
import Img from "gatsby-image";
import {SEO} from "theme-seo";

const Page = ({data,pageContext}) => {
    return (
        <Layout page={pageContext.pageName}>
            <SEO title={pageContext.title} seoTitle={pageContext.seoTitle} description={pageContext.description}/>
            {
                data.file && (data.file.extension === "svg" ?
                    <img style={{width:"100%"}} alt={data.file.name}
                         src={data.file.publicURL}   /> :
                    <Img alt={data.file.name}
                         fluid={data.file.childImageSharp.fluid}/>)


            }
            <div className="WordpressStyles my-24 mx-auto">
                <h1>{data.markdownRemark.frontmatter.title}</h1>
                <div className="w-10/12 lg:w-4/5 mx-auto" dangerouslySetInnerHTML={{__html: data.markdownRemark.html }}></div>
            </div>
        </Layout>
    );
};

export default Page;

export const query = graphql`
query ($pageSlug: String, $pageName: String) 
    {
       markdownRemark(fields: {slug: {regex: $pageSlug}})
       {
          html
          fields {
            slug
          }
          frontmatter {
            title
            order
          }  
      }
      
      file( name:{eq: $pageName}, extension:{ne:"md"}) {
      name 
      publicURL
      extension
      childImageSharp {
        fluid (maxWidth: 2500 ) {
          ...GatsbyImageSharpFluid
        }
      }
    }
  }
`;